'use client';

import { Container, Group, Button, Burger, Box, Text } from '@mantine/core';
import { useDisclosure } from '@mantine/hooks';
import Link from 'next/link';

export function Header() {
    const [opened, { toggle }] = useDisclosure(false);

    return (
        <Box
            component="header"
            style={{
                position: 'sticky',
                top: 0,
                zIndex: 100,
                backgroundColor: 'rgba(255, 255, 255, 0.8)',
                backdropFilter: 'blur(20px)',
                borderBottom: '1px solid rgba(0, 0, 0, 0.05)',
            }}
        >
            <Container size="lg" h={60}>
                <Group justify="space-between" h="100%">
                    <Text fw={700} component={Link} href="/" style={{ textDecoration: 'none', color: 'inherit', fontSize: '1.2rem' }}>
                        Nonghyup Broadcasting
                    </Text>

                    <Group gap="md" visibleFrom="xs">
                        <Button variant="subtle" color="gray" component={Link} href="/">Home</Button>
                        <Button variant="subtle" color="gray" component={Link} href="/facilities">Facilities</Button>
                        <Button variant="subtle" color="gray" component={Link} href="/schedule">Schedule</Button>
                    </Group>

                    <Burger opened={opened} onClick={toggle} hiddenFrom="xs" size="sm" />
                </Group>
            </Container>
        </Box>
    );
}
